$fontawesome_path: "." !default


@font-face {
  font-family: 'FontAwesome';
  src: url('#{$fontawesome_path}/fontawesome-webfont.eot'),
  url('#{$fontawesome_path}/fontawesome-webfont.eot?#iefix') format("embedded-opentype"),
  url('#{$fontawesome_path}/fontawesome-webfont.woff') format("woff"),
  url('#{$fontawesome_path}/fontawesome-webfont.ttf') format("truetype");
  font-weight: normal;
  font-style: normal;

}
%icon {
  font-family: FontAwesome;
  font-weight: normal;
  font-style: normal;
  display: inline-block;
  text-decoration: inherit;
  line-height: 1;
}

@mixin icon($icon){
  @extend %icon;
  content: $icon;

  }

@mixin icon-pseudo ($icon, $position: before) {

  @if ($position == after) {
    &:after {
      @include icon($icon);
      @content;
    }
  }
  @else if ($position == both) {
    &:before, &:after {
      @include icon($icon);
      @content;
    }
  }
  @else {
    &:before {
      @include icon($icon);
      @content;
    }
  }
}


